

#quote-box {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 40%;
  height: 50%;
}

#footer {
  margin-top: 20px;
  color: white;
}
.container a {
  display: inline-block;
  font-size: 40px;
  margin-right: 20px; 
}

.container #tweeter {
  margin-left: 20px;
  justify-self: left;
}

.container #buttons {
  justify-self: right;
  align-self: end;
  margin-right: 20px;
  margin-bottom: 8px;
}

#author {
  display: 'block';
  text-align: right;
  margin-right: 10px;
  font-size: 16px;
}

#quote {
  margin: auto;
  width: 90%;
  padding-top: 3%;
  font-size: 24px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


.container {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto;
  width: 30%;
  height: 50%;
}

@media(max-width: 600px){
  .quote-box {
    height: 80%;
    width: 80%;
  }

  .container {
    width: 80%;
    height: 20%;
  }

  #quote {
    margin: auto;
    padding-top: 3%;
    font-size: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
}

